import React, { useContext, useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "../styles/ThirdPage.css";
import { AppContext } from "../App";
import DeleteModal from "../components/DeleteModal";
import DeleteCheckModal from "../components/DeleteCheckModal";
// import { startReturnTimer, updateTimer } from "../assets/timer";
import { useKeyboardNavigation } from "../assets/useKeyboardNavigation";
import { useTextHandler } from "../assets/tts";

const ThirdPage = () => {
  const navigate = useNavigate();
  const {
    sections,
    totalMenuItems,
    isHighContrast,
    isLowScreen,
    quantities,
    handleIncrease,
    handleDecrease,
    filterMenuItems,
    isDeleteModal,
    setisDeleteModal,
    isDeleteCheckModal,
    setisDeleteCheckModal,
    commonScript,
    volume,
    convertToKoreanQuantity
  } = useContext(AppContext);
  const { handleText } = useTextHandler(volume);
  //    

  const ITEMS_PER_PAGE = isLowScreen ? 3 : 6; //   
  const [currentPage, setCurrentPage] = useState(1);
  const [id, setid] = useState(0);

  //     
  const startIndex = (currentPage - 1) * ITEMS_PER_PAGE;
  const priceItems = filterMenuItems(totalMenuItems, quantities);

  const currentItems = priceItems.slice(
    startIndex,
    startIndex + ITEMS_PER_PAGE
  );
  //    
  const totalPages =
    priceItems.length === 0 ? 1 : Math.ceil(priceItems.length / ITEMS_PER_PAGE);

  //   
  const handlePrevPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  const handleNextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };
  
  function prependRows(existingArray, currentItems) {
    // row1 rowN  
    const newRows = Array.from(
      { length: currentItems },
      (_, index) => `row${index + 1}`
    );

    //      
    return ["page", ...newRows, ...existingArray];
  }

  const focusableSections = prependRows(
    ["bottom", "footer", "bottomfooter"],
    currentItems.length
  );

  // useKeyboardNavigation
  const { updateFocusableSections } = useKeyboardNavigation({
    initFocusableSections: focusableSections,
    initFirstButtonSection: "row1",
  });

  const handleTouchDecrease = (id) => {
    if (quantities[id] === 1) {
      setid(id);
      if (currentItems.length > 1) {
        setisDeleteModal(true);
      } else {
        setisDeleteCheckModal(true);
      }
    } else {
      handleDecrease(id);
    }
  }

  const handleTouchDelete = (id)=>{
    setid(id);
    if (currentItems.length > 1) {
      setisDeleteModal(true);
    } else {
      setisDeleteCheckModal(true);
    }
  }


  useEffect(()=>{
     //   focusableSections 
     const updatedSections = prependRows(
      ["bottom", "footer", "bottomfooter"],
      currentItems.length
    );
    updateFocusableSections(updatedSections); //  

  },[currentPage])

  useEffect(() => {
    if (currentItems.length === 0) {
      navigate("/second");
    }
  }, [currentItems]);

  useEffect(() => {
    //      
    const timer = setTimeout(() => {
      if (document.activeElement) {
        const pageTTS = document.activeElement.dataset.text;
        setTimeout(() => {
          handleText(pageTTS);
        }, 500); // ""     0.5  
      }
      // startReturnTimer(commonScript.return, handleText, navigate);
    }, 0);

    return () => clearTimeout(timer); // 

    document.querySelectorAll('button').forEach(btn => {
      const { width, height } = btn.getBoundingClientRect();
      const shortSize = Math.min(width, height);
      btn.style.setProperty('--short-size', `${shortSize}px`);
    });
  }, []);



  return (
    <>
      {isDeleteModal ? (
        <DeleteModal
          currentItems={currentItems}
          quantities={quantities}
          id={id}
          handleDecrease={handleDecrease}
        ></DeleteModal>
      ) : (
        ""
      )}
      {isDeleteCheckModal ? (
        <DeleteCheckModal
          currentItems={currentItems}
          quantities={quantities}
          id={id}
          handleDecrease={handleDecrease}
        ></DeleteCheckModal>
      ) : (
        ""
      )}
      <div className="third-content">
        <div className="hidden-div" ref={sections.page}>
          <button
            type="hidden"
            autoFocus
            className="hidden-btn page-btn"
            data-text={" ,   ,  , ,  ,          .    , " +
          "   ,    ,    ,"  + commonScript.replay}
          ></button>
        </div>
        <div className="third-up-content">
          <span
            style={isHighContrast ? { color: "#FFE101" } : { color: "#8C532C" }}
          >
            
          </span>
           &nbsp;
          <span
            style={isHighContrast ? { color: "#FFE101" } : { color: "#8C532C" }}
          >
            
          </span>
          &nbsp; 
        </div>
        <div className="third-middle-content">
          {isLowScreen ? (
            <>
              <p style={{ marginLeft: "-30px" }}></p>
              <p style={{ marginLeft: "10px" }}></p>
              <p style={{ marginLeft: "-20px" }}></p>
              <p style={{ marginLeft: "-50px" }}></p>
            </>
          ) : (
            <>
              <p style={{ marginLeft: "110px" }}></p>
              <p style={{ marginLeft: "110px" }}></p>
              <p style={{ marginLeft: "55px" }}></p>
              <p style={{ marginLeft: "-20px" }}></p>
            </>
          )}
        </div>
        <div className="third-main-content">
          {currentItems.map((item, i) => {
            const globalIndex = startIndex + i + 1;
            const rowIndex = (i % ITEMS_PER_PAGE) + 1;
            const refKey = `row${rowIndex}`;
            return (
              <div key={item.id}>
                <div className="order-item" ref={sections[refKey]} data-text={`,${globalIndex}, ${item.name}, ${convertToKoreanQuantity(quantities[item.id])} , ${item.price * quantities[item.id]},   , `}>
                  <div
                    className="order-image-div"
                  >
                    <div className="order-index">{globalIndex}</div>
                    <img
                      src={item.img}
                      alt={item.name}
                      className="order-image"
                    />
                  </div>

                  <p className="order-name">{item.name}</p>
                  <div className="order-quantity">
                    <button
                      data-text=" "
                      className="qty-btn"
                      onTouchEnd={(e) => {
                        e.preventDefault();
                        e.currentTarget.focus();
                        handleTouchDecrease(item.id);
                      }}
                      onKeyDown={(e) => {
                        if (e.key === 'Enter') {
                          e.preventDefault();
                          handleText('', false);
                          setTimeout(() => handleTouchDecrease(item.id), 100);
                        }
                      }}
                    >
                      -
                    </button>
                    <span className="qty">{quantities[item.id]}</span>
                    <button
                      data-text=" "
                      className="qty-btn"
                      onTouchEnd={(e) => {
                        e.preventDefault();
                        e.currentTarget.focus();
                        handleIncrease(item.id);
                      }}
                      onKeyDown={(e) => {
                        if (e.key === 'Enter') {
                          e.preventDefault();
                          handleText('', false);
                          setTimeout(() => handleIncrease(item.id), 100);
                        }
                      }}
                    >
                      +
                    </button>
                  </div>
                  <span className="order-price">
                    {Number(item.price * quantities[item.id]).toLocaleString()}
                  </span>
                  <button
                    data-text=""
                    className="delete-btn"
                    onTouchEnd={(e) => {
                      e.preventDefault();
                      e.currentTarget.focus();
                      handleTouchDelete(item.id);
                    }}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter') {
                        e.preventDefault();
                        handleText('', false);
                        setTimeout(() => handleTouchDelete(item.id), 100);
                      }
                    }}
                  >
                    <img src= "/images/trash.png"></img>
                  </button>
                </div>

                <div className="row-line"></div>
              </div>
            );
          })}
        </div>
        <div
          className="pagination2"
          ref={sections.bottom}
          data-text={`, , ${totalPages}  ${currentPage},   ,`}
        >
          <button data-text=" ," onTouchEnd={(e) => { e.preventDefault();e.target.focus(); handlePrevPage(); }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(handlePrevPage, 100);
              }
            }}>
            &lt;&nbsp; 
          </button>
          <span style={{ fontSize: "4rem" }}>
            <span
              style={
                isHighContrast ? { color: "#FFE101" } : { color: "#8C532C" }
              }
            >
              {currentPage}
            </span>
            <span style={{ color: "#707070" }}>&nbsp;/&nbsp;</span>
            <span style={{ color: "#707070" }}>{totalPages}</span>
          </span>
          <button data-text=" ," 
          onTouchEnd={(e) => { e.preventDefault();e.target.focus(); handleNextPage(); }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(handleNextPage, 100);
              }
            }}>
             &nbsp;&gt;
          </button>
        </div>
      </div>
    </>
  );
};

export default ThirdPage;
