import React, { useContext, useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "../styles/ForthPage.css";
import { AppContext } from "../App";
// import { startReturnTimer, updateTimer } from "../assets/timer";
import { useKeyboardNavigation } from "../assets/useKeyboardNavigation";
import { useTextHandler } from "../assets/tts";

const ForthPage = () => {
  const {
    sections,
    totalSum,
    isLowScreen,
    setisLowScreen,
    isHighContrast,
    setisHighContrast,
    isCreditPayContent,
    setisCreditPayContent,
    commonScript,
    totalMenuItems,
    quantities,
    setQuantities,
    createOrderItems,
    volume,
    setVolume,
    isBigSize,
    setisBigSize,
    setisReturnModal,
    setisAccessibilityModal
  } = useContext(AppContext);
  const navigate = useNavigate();
  const orderItems = createOrderItems(totalMenuItems, quantities);
  const { handleText } = useTextHandler(volume);
  const [countdown, setCountdown] = useState(60);
  const [orderNum, setOrderNum] = useState(0);

  useEffect(() => {
    if (isCreditPayContent !== 0) {
      setisCreditPayContent(0);
    }
  }, []);

  useEffect(() => {
    document.querySelector('.hidden-btn').focus();
    if (document.activeElement) {
      const pageTTS = document.activeElement.dataset.text;
      // document.activeElement.blur(); //   
      setTimeout(() => {
        handleText(pageTTS);
      }, 500); // ""     
    }
    // startReturnTimer(commonScript.return, handleText, navigate);

    //   :   60    
    if (isCreditPayContent === 4 || isCreditPayContent === 6) {
      const resetCountdown = () => setCountdown(60); //   

      //  
      setCountdown(60);
      const timer = setInterval(() => {
        setCountdown((prev) => {
          if (prev === 0) {
            clearInterval(timer);
            setTimeout(() => {
              setisCreditPayContent(7);
            }, 0);
            return 0;
          }
          return prev - 1;
        });
      }, 1000);

      // keydown  click  
      const handleReset = () => resetCountdown();
      window.addEventListener('keydown', handleReset);
      window.addEventListener('click', handleReset);

      return () => {
        //      
        window.removeEventListener('keydown', handleReset);
        window.removeEventListener('click', handleReset);
        clearInterval(timer);
      };
    }

    //   :   3   

    if (isCreditPayContent === 7) {
      setCountdown(4);
      const timer = setInterval(() => {
        setCountdown((prev) => {
          if (prev === 0) {
            clearInterval(timer);
            setTimeout(() => {
              //  
              setisReturnModal(false);
              setisAccessibilityModal(false);
              setQuantities(
                totalMenuItems.reduce((acc, item) => ({ ...acc, [item.id]: 0 }), {})
              );

              // 
              setisHighContrast(false);
              setVolume(1);
              setisBigSize(false);
              setisLowScreen(false);
              navigate("/");
              return 0;
            }, 0);
          }
          return prev - 1;
        });
      }, 1000);

      return () => clearInterval(timer);
    }
    document.querySelectorAll('button').forEach(btn => {
      const { width, height } = btn.getBoundingClientRect();
      const shortSize = Math.min(width, height);
      btn.style.setProperty('--short-size', `${shortSize}px`);
    });
  }, [isCreditPayContent]);

  useEffect(() => {
    if (window.chrome.webview) {
      window.chrome.webview.addEventListener("message", (event) => {
        let resData = event.data;
        //  
        //  :  ->    : setisCreditPayContent(3) -> setisCreditPayContent(4)
        //  :    : setisCreditPayContent(4)
        if (resData.arg.result === "SUCCESS") {
          if (resData.Command === "PAY") {
            setisCreditPayContent(3); //    
          }
          if (resData.Command === "PRINT") {
            setisCreditPayContent(4); //   
          }
        } else {
          console.log(resData.arg.errorMessage);
        }
      });
    }
  });

  //   
  const sendOrderDataToApp = (paymentType) => {
    var arr_order_data = [];
    orderItems.forEach((item) => {
      arr_order_data.push({
        menuName: item.name,
        quantity: item.quantity,
        price: item.price * item.quantity,
      });
    });
    const supplyPrice = (totalSum / 1.1).toFixed(2);
    var cmd_val = {
      orderData: arr_order_data,
      totalPrice: totalSum,
      supplyPrice: supplyPrice,
      tax: (totalSum - supplyPrice).toFixed(2),
      paymentType: paymentType,
      orderNumber: updateOrderNumber(),
    };
    setCallWebToApp('PAY', cmd_val);
  }

  const sendPrintReceiptToApp = () => {
    setCallWebToApp('PRINT', '');
  }

  const sendCancelPayment = () => {
    setCallWebToApp("CANCEL", "");
  };

  const setCallWebToApp = (p_cmd, p_val) => {
    var obj_cmd = {
      Command: p_cmd,
      arg: p_val,
    };

    console.log("obj_cmd: " + JSON.stringify(obj_cmd));

    if (window.chrome.webview) {
      window.chrome.webview.postMessage(JSON.stringify(obj_cmd));
    }
  };

  //     
  const updateOrderNumber = () => {
    let tmpOrderNum = localStorage.getItem("ordernum");
    if (tmpOrderNum === null) {
      tmpOrderNum = 1;
    } else {
      tmpOrderNum = parseInt(tmpOrderNum, 10) + 1;
    }

    localStorage.setItem("ordernum", tmpOrderNum);
    setOrderNum(tmpOrderNum);

    return tmpOrderNum;
  };

  // //   
  // const getOrderNumber = () => {
  //   return localStorage.getItem("ordernum");
  // }

  // useKeyboardNavigation
  useKeyboardNavigation({
    initFocusableSections: ["page", "middle", "bottom", "bottomfooter"],
    initFirstButtonSection: "page",
    // isAccessibilityModal,
    // isCreditPayContent
  });

  //isCreditPayContent
  // 0:    
  // 1:   ()-> ()
  // 2:  -> ()
  // 3:   ()
  // 4:     (  )
  // 5:   ()
  // 6:  
  // 7:  

  const getPageScript = () => {
    if (isCreditPayContent === 0) {
      return ` ,   .  , ${totalSum.toLocaleString(
        "ko-KR"
      )},   .    ,    . ` + commonScript.replay;
    } else if (isCreditPayContent === 1) {
      return `,  ,       ,    ,     , ` + commonScript.replay;
    } else if (isCreditPayContent === 2) {
      return ` ,  ,        ,     ,     , ` + commonScript.replay;
    } else if (isCreditPayContent === 3) {
      return ` ,  ,  ,     ,   ,   ,      , ` + commonScript.replay;
    } else if (isCreditPayContent === 4) {
      return ` ,  , ,  ${orderNum},     ,    ,     ,    ,           ,` + commonScript.replay;
    } else if (isCreditPayContent === 5) {
      return ` , , , ${orderNum},     .    .       . ` + commonScript.replay;
    } else if (isCreditPayContent === 6) {
      return ` ,  ,     ,      ,     ,   ,           ,` + commonScript.replay;
    } else if (isCreditPayContent === 7) {
      return `, ,  ,`;
    } else {
      return "";
    }
  };

  return (
    <div className="forth-content">
      <div className="hidden-div" ref={sections.page}>
        <button
          type="hidden"
          className="hidden-btn page-btn"
          autoFocus
          data-text={getPageScript()}
        />
      </div>
      {isCreditPayContent === 0 ? (
        <>
          <div className="forth-up-content">
            <span className="highlight-text">
              
            </span>
             
          </div>
          <div
            className="forth-middle-content"
            onTouchEnd={(e) => {
              e.preventDefault();
              e.target.focus();
              updateOrderNumber();
              setisCreditPayContent(4);
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(() => {
                  updateOrderNumber();
                  setisCreditPayContent(4);
                }, 100);
              }
            }}
          >
            <span></span>
            <span style={{ fontSize: "8rem" }}>
              {totalSum.toLocaleString("ko-KR")}
            </span>
          </div>
          <div className="forth-main-content">
            <div
              className="forth-main-flex"
              ref={sections.middle}
              data-text=" .   , "
            >
              <button
                data-text=","
                className="pay-type-div"
                onTouchEnd={(e) => {
                  e.preventDefault();
                  e.target.focus();
                  sendOrderDataToApp("card");
                  setisCreditPayContent(1);
                }}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleText(', ', false);
                    setTimeout(() => {
                      sendOrderDataToApp("card");
                      setisCreditPayContent(1);
                    }, 100);
                  }
                }}
              >
                <img
                  style={
                    isLowScreen
                      ? { width: "100px", height: "65px" }
                      : { width: "125px", height: "85px" }
                  }
                  src="/images/img_credit_card.png"
                  alt="card"
                ></img>
                <p></p>
              </button>
              <button
                className="pay-type-div"
                data-text=","
                onTouchEnd={(e) => {
                  e.preventDefault();
                  e.target.focus();
                  sendOrderDataToApp("mobile");
                  setisCreditPayContent(2);
                }}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleText(', ', false);
                    setTimeout(() => {
                      sendOrderDataToApp("mobile");
                      setisCreditPayContent(2);
                    }, 100);
                  }
                }}
              >
                <img
                  style={
                    isLowScreen
                      ? { width: "77px", height: "130px" }
                      : { width: "110px", height: "200px" }
                  }
                  src="/images/img_Mpay.png"
                  alt="mobile"
                ></img>
                <p style={{ marginTop: "-50px" }}> </p>
              </button>
              {/* <div className="pay-type-div">
                <img
                  style={{ width: "110px", height: "200px" }}
                  src="/images/img_QRpay.png"
                  alt="qr"
                ></img>
                <p>QR </p>
              </div> */}
            </div>
            <div
              ref={sections.bottom}
              className="flex-center"
              data-text=".   ,"
            >
              <button
                data-text=","
                className="forth-main-btn"
                onTouchEnd={(e) => {
                  e.preventDefault();
                  navigate("/third");
                }}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleText(', ', false);
                    setTimeout(() => {
                      navigate("/third");
                    }, 100);
                  }
                }}
              >
                
              </button>
            </div>
          </div>
        </>
      ) : isCreditPayContent === 1 ? (
        <div
          data-text=" ,   ,"
          ref={sections.bottom}
          className="credit-pay-content"
        >
          <div className="credit-pay-text">
            <div>
                {" "}
              <span className="highlight-text">{isLowScreen && isBigSize ? <br /> : ''}</span>
              {isLowScreen && !isBigSize ? (
                <>
                  <br />
                  <div className="flex-center"></div>
                </>
              ) : (
                ""
              )}
            </div>
            <div>
              <span className="highlight-text">
                
              </span>
                
            </div>
          </div>
          <img onClick={() => setisCreditPayContent(3)}
            className="credit-pay-image"
            src={"/images/img_card_in.png"}
          ></img>
          <button
            data-text=""
            className="forth-main-btn2"
            onTouchEnd={(e) => {
              e.preventDefault();
              e.target.focus();
              // setisCreditPayContent(3);
              sendCancelPayment();
              setisCreditPayContent(0);
              // navigate("/third");
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(() => {
                  sendCancelPayment();
                  setisCreditPayContent(0);
                }, 100);
              }
            }}
          >
            
          </button>
        </div>
      ) : isCreditPayContent === 2 ? (
        <div
          data-text=" ,   ,"
          ref={sections.bottom}
          className="credit-pay-content"
        >
          <div className="credit-pay-text">
            <div>
                {" "}
              <span className="highlight-text">{isLowScreen && isBigSize ? <br /> : ''}</span>
              {isLowScreen && !isBigSize ? (
                <>
                  <br></br>
                  <div className="flex-center"></div>
                </>
              ) : (
                ""
              )}
            </div>
            <div>
              {" "}
              <span className="highlight-text" >
                
              </span>
              {isLowScreen && isBigSize ? <br /> : ''}  {isLowScreen && !isBigSize ? <><br></br><div className="flex-center"></div> </> : ""}
            </div>
          </div>
          <img onClick={() => setisCreditPayContent(4)}
            className="credit-pay-image"
            src={"/images/img_Mpay_big 1.png"}
          ></img>
          <button
            data-text=""
            className="forth-main-btn2"
            onTouchEnd={(e) => {
              e.preventDefault();
              e.target.focus();
              // setisCreditPayContent(4)
              sendCancelPayment();
              setisCreditPayContent(0);
              // navigate("/third");
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(() => {
                  sendCancelPayment();
                  setisCreditPayContent(0);
                }, 100);
              }
            }}
          >
            
          </button>
        </div>
      ) : isCreditPayContent === 3 ? (
        <div
          data-text=" ,   ,"
          ref={sections.bottom}
          className="credit-pay-content"
        >
          <div className="credit-pay-text">
            <div>
              <span className="highlight-text">
                
              </span>
               .
              {/*  &nbsp;
              <span className="highlight-text"></span>
              &nbsp;{isLowScreen && isBigSize? <br/>: '' } 
              {isLowScreen && !isBigSize ? <><br></br><div className="flex-center"></div> </>: ""} */}

            </div>
          </div>
          <img onClick={() => setisCreditPayContent(4)}
            className="credit-pay-image"
            src={"/images/img_card_out.png"}
          ></img>
          {/* <button
            data-text=""
            className="forth-main-btn2 btn-confirm"
            onTouchEnd={(e) => {
              e.preventDefault();
              e.target.focus();
              handleText(' , ')
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(' , ');
              }
            }}
          >
            
          </button> */}
        </div>
      ) : isCreditPayContent === 4 ? (
        <div
          data-text=" ,   ,"
          ref={sections.bottom}
          className="credit-pay-content"
        >
          <div className="credit-pay-text">
            <div>
              <span className="highlight-text">
                
              </span>
            </div>
            <div>
                {" "}
              <span className="highlight-text">
                {isLowScreen && isBigSize ? <br /> : ''}
              </span>
              {isLowScreen && !isBigSize ? <br /> : ""} 
            </div>
            <div>
              <span
                style={
                  isHighContrast ? { color: "#FFE101" } : { color: "#8C532C" }
                }
              >
                 
              </span>
               
            </div>
          </div>
          <img
            className="credit-pay-image"
            src={"/images/img_order_paper.png"}
          ></img>
          <div className="order-num-txt">
            {/* <span>{orderNum}</span> */}
            <span>100</span>
          </div>
          <div className="forth-main-two-btn">
            <button
              data-text=" ,"
              className="forth-main-two-btn1"
              onTouchEnd={(e) => {
                e.preventDefault();
                e.target.focus();
                sendPrintReceiptToApp();
                setisCreditPayContent(6);
              }}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  handleText(', ', false);
                  setTimeout(() => {
                    sendPrintReceiptToApp();
                    setisCreditPayContent(6);
                  }, 100);
                }
              }}
            >
               
            </button>
            <button
              data-text=" ,"
              className="forth-main-two-btn2"
              onTouchEnd={(e) => {
                e.preventDefault();
                e.target.focus();
                setisCreditPayContent(7)
              }}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  handleText(', ', false);
                  setTimeout(() => {
                    setisCreditPayContent(7)
                  }, 100);
                }
              }}
            >
               {countdown}
            </button>
          </div>
        </div>
      ) : isCreditPayContent === 5 ? (  //  
        <div
          data-text=" ,   ,"
          ref={sections.bottom}
          className="credit-pay-content"
        >
          <div className="credit-pay-text">
            <div>
               {" "}
              <span
                style={
                  isHighContrast ? { color: "#FFE101" } : { color: "#8C532C" }
                }
              >
                
              </span>
              {" "}
              <span className="highlight-text">
                
              </span>
               
            </div>
            <div>
               {" "}
              <span
                style={
                  isHighContrast ? { color: "#FFE101" } : { color: "#8C532C" }
                }
              >
                
              </span>
              &nbsp;
            </div>
          </div>
          <img
            className="credit-pay-image"
            src={"/images/img_order_paper.png"}
          ></img>
          <div className="order-num-txt">
            <span>{orderNum}</span>
          </div>
          <button
            data-text=""
            className="forth-main-btn2 btn-confirm"
            onTouchEnd={(e) => {
              e.preventDefault();
              e.target.focus();
              setisCreditPayContent(7)
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(() => {
                  setisCreditPayContent(7)
                }, 100);
              }
            }}
          >
            
          </button>
        </div>
      ) : isCreditPayContent === 6 ? (
        <div
          data-text=" ,   ,"
          className="credit-pay-content"
          ref={sections.bottom}
        >
          <div className="credit-pay-text">
            <div>
               {" "}
              <span className="highlight-text">
                
              </span>
              {" "}
              <span className="highlight-text">
                {isLowScreen && isBigSize ? <br /> : ''}
              </span>
              {isLowScreen && !isBigSize ? <br /> : ''} 
            </div>
            <div>
              <span className="highlight-text">
                
              </span>
              &nbsp; {isLowScreen && isBigSize ? <br /> : ''}.
            </div>
          </div>
          <img
            className="credit-pay-image"
            src={"/images/img_receipt.png"}
          ></img>
          {/* <div className="order-num-txt">
            <span>{orderNum}</span>
          </div> */}
          <button
            data-text=""
            className="forth-main-btn2 btn-confirm"
            onTouchEnd={(e) => {
              e.preventDefault();
              e.target.focus();
              setisCreditPayContent(7)
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleText(', ', false);
                setTimeout(() => {
                  setisCreditPayContent(7)
                }, 100);
              }
            }}
          >
            {countdown}
          </button>
        </div>
      ) : isCreditPayContent === 7 ? (
        <div className="credit-pay-content">
          <div className="credit-pay-text2">
            <div>  </div>
            {/* <div>
              <span className="highlight-text">
                  
              </span>
                {isLowScreen ? <><br></br><div className="flex-center"></div> </>: ""}
            </div> */}
          </div>
          <img
            className="end-checked-image"
            src={
              isHighContrast
                ? "/images/contrast_ico_end.png"
                : "/images/ico_end.png"
            }
          ></img>
          <div className="return-num-txt">
            <span>{countdown <= 0 ? '' : countdown}</span>
          </div>
          {/* <img
            className="end-logo-image"
            src={"/images/logo_grey_big.png"}
          ></img> */}
        </div>
      ) : (
        <></>
      )}
    </div>
  );
};

export default ForthPage;

