import React, { useContext, useEffect, useState } from "react";
import { AppContext } from "../App";
import { useNavigate } from "react-router-dom";
// import { updateTimer } from "../assets/timer";
import { useTextHandler } from '../assets/tts';

const DeleteModal = ({ handleDecrease, id, quantities, currentItems }) => {
  const navigate = useNavigate();
  const {
    sections,
    isHighContrast,
    isDeleteModal,
    setisDeleteModal,
    volume,
    commonScript,
    readCurrentPage
  } = useContext(AppContext);
  const { handleText } = useTextHandler(volume);

  useEffect(() => {
    if (document.activeElement) {
      const pageTTS = document.activeElement.dataset.text;
      // document.activeElement.blur(); //   
      setTimeout(() => {
        handleText(pageTTS);
      }, 500); // ""     
    }
  }, [isDeleteModal]);

  const handleTouchDeleteButton = (id)=>{
    if (quantities[id] !== 1) {
      quantities[id] = 0;
    } else {
      handleDecrease(id);
    }

    setisDeleteModal(false);
    readCurrentPage();
  }

  if (isDeleteModal) {
    return (
      <>
        <div className="hidden-div" ref={sections.modalPage}>
          <button type="hidden" autoFocus className="hidden-btn" data-text={", , ,  ,     , " + commonScript.replay}></button>
        </div>
        <div
          className="return-modal-overlay"
        ></div>
        <div
          className="return-modal-content"
        >
          <img
            className="return-modal-image"
            src={
              isHighContrast
                ? "/images/contrast-Group 13.png"
                : "/images/ico_notice.png"
            }
          ></img>
          <div
            className="return-modal-message"
          >
            <p>
              {" "}
              <span
                className="return-highlight"
                style={
                  isHighContrast ? { color: "#FFE101" } : { color: "#A4693F" }
                }
              >
                
              </span>
              
            </p>
            <p>
               {" "}
              <span
                className="return-highlight"
                style={
                  isHighContrast ? { color: "#FFE101" } : { color: "#A4693F" }
                }
              >
                
              </span>{" "}
               
            </p>
          </div>
          <div data-text=" ,   , "
            ref={sections.confirmSections} className="return-modal-buttons">
            <button data-text=","
              className="return-btn-cancel"
              onTouchEnd={(e) => { e.preventDefault(); setisDeleteModal(false);readCurrentPage();}}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  handleText(', ', false);
                  setTimeout(() => { setisDeleteModal(false); readCurrentPage(); },300);
                }
              }}
            >
              
            </button>
            <button data-text=","
              className="return-btn-confirm"
              onTouchEnd={(e) => { e.preventDefault(); handleTouchDeleteButton(id)}}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  handleText(', ', false);
                  setTimeout(() => { handleTouchDeleteButton(id);},300);
                }
              }}
            >
              
            </button>
          </div>
        </div>
      </>
    );
  }
};

export default DeleteModal;
