import React, { useState, useRef, useContext, useEffect } from "react";
import { AppContext } from "../App";
import FocusTrap from "focus-trap-react";
import { useTextHandler } from '../assets/tts';

const AccessibilityModal = ({ }) => {
  const {
    accessibility,
    setAccessibility,
    sections,
    isLowScreen,
    setisLowScreen,
    isHighContrast,
    setisHighContrast,
    isAccessibilityModal,
    setisAccessibilityModal,
    volume,
    setVolume,
    isBigSize,
    setisBigSize,
    commonScript,
    readCurrentPage
  } = useContext(AppContext);

  const { handleText } = useTextHandler(volume);
  const volumeMap = {
    0: '',
    1: '',
    2: '',
    3: ''
  };

  const [prevAccessibility, setPrevAccessibility] = useState({
    isHighContrast: isHighContrast,
    isLowScreen: isLowScreen,
    isBigSize: isBigSize,
    volume: volume
  });

  const setTmpVolume = (vol) => {
    // const audio = document.getElementById('audioPlayer');
    // if(!audio.paused){
    //   audio.volume = vol;
    // }
  }

  const handleTouchSetAccessibility = ()=>{
    const volumeValue = {
      0: '0',
      1: '0.5',
      2: '0.75',
      3: '1'
    };
    const audioPlayer = document.getElementById('audioPlayer');
    audioPlayer.volume = volumeValue[prevAccessibility.volume];

    setisHighContrast(prevAccessibility.isHighContrast);
    setVolume(prevAccessibility.volume);
    setisBigSize(prevAccessibility.isBigSize)
    setisLowScreen(prevAccessibility.isLowScreen);
    setAccessibility(prevAccessibility);
    setisAccessibilityModal(false);
    readCurrentPage(prevAccessibility.volume);
  }

  useEffect(() => {
    if (document.activeElement) {
      const pageTTS = document.activeElement.dataset.text;
      setTimeout(() => {
        handleText(pageTTS);
      }, 500); // ""     0.5  
    }
  }, [isAccessibilityModal]);


  if (isAccessibilityModal) {
    return (
      <>
        <div className="hidden-div" ref={sections.modalPage}>
          <button type="hidden" autoFocus className="hidden-btn" data-text={', , ,    ,   ,' + commonScript.replay}></button>
        </div>
        <div
          className="accessibility-modal-overlay"
        ></div>
        <div
          className={`accessibility-modal-content ${isLowScreen ? "reverse" : ""
            }
          `}
        >
          <div
            className="accessibility-up-content"
          >
            <div
              style={{ color: "#E7E3E0", fontSize: "6rem", fontWeight: "600" }}
            >
              
            </div>
            <div>
              <div style={{ color: "#E7E3E0", fontSize: "4.1rem" }}>
                &nbsp;
                <span
                  style={
                    isHighContrast
                      ? { color: "#FFE101", fontSize: "4.1rem" }
                      : { color: "#EB9B63", fontSize: "4.1rem" }
                  }
                >
                   
                </span>
                 
              </div>
              <div
                style={{
                  color: "#E7E3E0",
                  textAlign: "center",
                  marginTop: "3px",
                }}
              >
                <span
                  style={
                    isHighContrast ? { color: "#FFE101" } : { color: "#EB9B63" }
                  }
                >
                  
                </span>
                &nbsp; 
              </div>
            </div>
          </div>
          <div
            className="accessibility-down-content"
          >
            <div
              className="accessibility-down-content-div1" data-text=" ,   , "
              ref={sections.AccessibilitySections1}
            >
              <p>  </p>
              <button data-text=","
                onTouchEnd={(e) => { e.preventDefault(); e.target.focus(); setPrevAccessibility({ isHighContrast: false, isLowScreen: false, volume: 1, isBigSize: false }); }}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleText(', ', false);
                    setTimeout(() => {
                      setPrevAccessibility({ isHighContrast: false, isLowScreen: false, volume: 1, isBigSize: false });
                    }, 100);
                  }
                }}
                className="accessibility-down-content-div-btn"
              >
                
              </button>
            </div>
            <div className="accessibility-down-content-line"></div>
            <div className="accessibility-down-content-div2">
              <div className="flex" style={{ alignItems: "center" }}>
                <img
                  src={
                    isHighContrast
                      ? "/images/contrast_ico_high_cont.png"
                      : "/images/ico_high_cont.png"
                  }
                  className="accessibility-down-content-div-img"
                ></img>
                <p></p>
              </div>
              <div
                className="flex"
                style={{ gap: "10px" }}
                ref={sections.AccessibilitySections2}
                data-text={` , , ${prevAccessibility.isHighContrast ? '' : ''},   ,`}
              >
                <button data-text={`, ${prevAccessibility.isHighContrast ? ', ' : ', '}`}
                  className={`select-btn accessibility-down-content-div-btn1 
                    ${prevAccessibility.isHighContrast ? "" : "accessibility-btn-active"}`}
                  onTouchEnd={(e) => { e.preventDefault(); e.target.focus(); setPrevAccessibility(prevState => ({ ...prevState, isHighContrast: false })); }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleText(', ', false);
                      setTimeout(() => {
                        setPrevAccessibility(prevState => ({ ...prevState, isHighContrast: false }));
                      }, 100);
                    }
                  }}
                >
                  
                </button>
                <button data-text={`, ${prevAccessibility.isHighContrast ? ', ' : ', '}`}
                  className={`select-btn accessibility-down-content-div-btn1 
                    ${prevAccessibility.isHighContrast ? "accessibility-btn-active" : ""}`}
                  onTouchEnd={(e) => { e.preventDefault(); e.target.focus(); setPrevAccessibility(prevState => ({ ...prevState, isHighContrast: true })); }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleText(', ', false);
                      setTimeout(() => {
                        setPrevAccessibility(prevState => ({ ...prevState, isHighContrast: true }));
                      }, 100);
                    }
                  }}
                >
                  
                </button>
              </div>
            </div>
            <div className="accessibility-down-content-line"></div>
            <div className="accessibility-down-content-div3">
              <div className="flex" style={{ alignItems: "center" }}>
                <img
                  src={
                    isHighContrast
                      ? "/images/contrast_ico_volume.png"
                      : "/images/ico_volume.png"
                  }
                  className="accessibility-down-content-div-img"
                ></img>
                <p></p>
              </div>
              <div
                className="flex"
                style={{ gap: "10px" }}
                ref={sections.AccessibilitySections3}
                data-text={`, , ${volumeMap[prevAccessibility.volume]},   , `}
              >
                <button data-text={`, ${prevAccessibility.volume === 0 ? ', ' : ', '}`}
                  className={`select-btn accessibility-down-content-div-btn2 ${prevAccessibility.volume === 0 ? "accessibility-btn-active" : ""}`}
                  onTouchEnd={(e) => {
                    e.preventDefault();
                    e.target.focus();
                    setPrevAccessibility(prevState => ({ ...prevState, volume: 0 }));
                    setTmpVolume(0);
                  }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleText(', ', false);
                      setTimeout(() => {
                        setPrevAccessibility(prevState => ({ ...prevState, volume: 0 }));
                        setTmpVolume(0);
                      }, 100);
                    }
                  }}
                >
                  
                </button>
                <button data-text={`, ${prevAccessibility.volume === 1 ? ', ' : ', '}`}
                  className={`select-btn accessibility-down-content-div-btn2 ${prevAccessibility.volume === 1 ? "accessibility-btn-active" : ""}`}
                  onTouchEnd={(e) => {
                    e.preventDefault();
                    e.target.focus();
                    setPrevAccessibility(prevState => ({ ...prevState, volume: 1 }));
                    setTmpVolume(0.5);
                  }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleText(', ', false);
                      setTimeout(() => {
                        setPrevAccessibility(prevState => ({ ...prevState, volume: 1 }));
                        setTmpVolume(0.5);
                      }, 100);
                    }
                  }}
                >
                  
                </button>
                <button data-text={`, ${prevAccessibility.volume === 2 ? ', ' : ', '}`}
                  className={`select-btn accessibility-down-content-div-btn2 ${prevAccessibility.volume === 2 ? "accessibility-btn-active" : ""}`}
                  onTouchEnd={(e) => {
                    e.preventDefault();
                    e.target.focus();
                    setPrevAccessibility(prevState => ({ ...prevState, volume: 2 }));
                    setTmpVolume(0.75);
                  }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleText(', ', false);
                      setTimeout(() => {
                        setPrevAccessibility(prevState => ({ ...prevState, volume: 2 }));
                    setTmpVolume(0.75);
                      }, 100);
                    }
                  }}
                >
                  
                </button>
                <button data-text={`, ${prevAccessibility.volume === 3 ? ', ' : ', '}`}
                  className={`select-btn accessibility-down-content-div-btn2 ${prevAccessibility.volume === 3 ? "accessibility-btn-active" : ""}`}
                  onTouchEnd={(e) => {
                    e.preventDefault();
                    e.target.focus();
                    setPrevAccessibility(prevState => ({ ...prevState, volume: 3 }));
                    setTmpVolume(1);
                  }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleText(', ', false);
                      setTimeout(() => {
                        setPrevAccessibility(prevState => ({ ...prevState, volume: 3 }));
                        setTmpVolume(1);
                      }, 100);
                    }
                  }}
                >
                  
                </button>
              </div>
            </div>
            <div className="accessibility-down-content-line"></div>
            <div className="accessibility-down-content-div4">
              <div className="flex" style={{ alignItems: "center" }}>
                <img
                  src={
                    isHighContrast
                      ? "/images/contrast_ico_zoom.png"
                      : "/images/ico_zoom.png"
                  }
                  className="accessibility-down-content-div-img"
                ></img>
                <p> </p>
              </div>
              <div
                className="flex"
                style={{ gap: "10px" }}
                ref={sections.AccessibilitySections4}
                data-text={` , , ${prevAccessibility.isBigSize ? '' : ''},   , `}
              >
                <button data-text={`, ${prevAccessibility.isBigSize ? ', ' : ', '}`}
                  className={`select-btn accessibility-down-content-div-btn1 ${prevAccessibility.isBigSize ? "" : "accessibility-btn-active"}`}
                  onTouchEnd={(e) => {
                    e.preventDefault();
                    e.target.focus();
                    setPrevAccessibility(prevState => ({ ...prevState, isBigSize: false }));
                  }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleText(', ', false);
                      setTimeout(() => {
                        setPrevAccessibility(prevState => ({ ...prevState, isBigSize: false }));
                      }, 100);
                    }
                  }}
                >
                  
                </button>
                <button data-text={`, ${prevAccessibility.isBigSize ? ', ' : ', '}`}
                  className={`select-btn accessibility-down-content-div-btn1 ${prevAccessibility.isBigSize ? "accessibility-btn-active" : ""}`}
                  onTouchEnd={(e) => {
                    e.preventDefault();
                    e.target.focus();
                    setPrevAccessibility(prevState => ({ ...prevState, isBigSize: true }));
                  }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleText(', ', false);
                      setTimeout(() => {
                        setPrevAccessibility(prevState => ({ ...prevState, isBigSize: true }));
                      }, 100);
                    }
                  }}
                >
                  
                </button>
              </div>
            </div>
            <div className="accessibility-down-content-line"></div>

            <div className="accessibility-down-content-div5">
              <div className="flex" style={{ alignItems: "center" }}>
                <img
                  src={
                    isHighContrast
                      ? "/images/contrast_ico_low_sc.png"
                      : "/images/ico_low_sc.png"
                  }
                  className="accessibility-down-content-div-img"
                ></img>
                <p></p>
              </div>
              <div
                className="flex"
                style={{ gap: "10px" }}
                ref={sections.AccessibilitySections5}
                data-text={` , , ${prevAccessibility.isLowScreen ? '' : ''},   , `}
              >
                <button data-text={`, ${prevAccessibility.isBigSize ? ', ' : ', '}`}
                  className={`select-btn accessibility-down-content-div-btn1 ${prevAccessibility.isLowScreen ? "" : "accessibility-btn-active"}`}
                  onTouchEnd={(e) => {
                    e.preventDefault();
                    e.target.focus();
                    setPrevAccessibility(prevState => ({ ...prevState, isLowScreen: false }));
                  }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleText(', ', false);
                      setTimeout(() => {
                        setPrevAccessibility(prevState => ({ ...prevState, isLowScreen: false }));
                      }, 100);
                    }
                  }}
                >
                  
                </button>
                <button data-text={`, ${prevAccessibility.isBigSize ? ', ' : ', '}`}
                  className={`select-btn accessibility-down-content-div-btn1 ${prevAccessibility.isLowScreen ? "accessibility-btn-active" : ""}`}
                  onTouchEnd={(e) => {
                    e.preventDefault();
                    e.target.focus();
                    setPrevAccessibility(prevState => ({ ...prevState, isLowScreen: true }));
                  }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleText(', ', false);
                      setTimeout(() => {
                        setPrevAccessibility(prevState => ({ ...prevState, isLowScreen: true }));
                      }, 100);
                    }
                  }}
                >
                  
                </button>
              </div>
            </div>
            <div
              className="accessibility-modal-buttons"
              ref={sections.AccessibilitySections6}
              data-text=" ,   , "
            >
              <button data-text=", "
                className="accessibility-btn-cancel"
                onTouchEnd={(e) => {
                  e.preventDefault();
                  setAccessibility({ isHighContrast: isHighContrast, volume: volume, isBigSize: isBigSize, isLowScreen: isLowScreen });
                  setisAccessibilityModal(false);
                  readCurrentPage();
                }}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleText(', ', false);
                    setTimeout(() => {
                      setAccessibility({ isHighContrast: isHighContrast, volume: volume, isBigSize: isBigSize, isLowScreen: isLowScreen });
                      setisAccessibilityModal(false);
                      readCurrentPage();
                    }, 300);
                  }
                }}
              >
                
              </button>
              <button data-text=", "
                className="accessibility-btn-confirm"
                onTouchEnd={(e) => {
                  e.preventDefault();
                  handleTouchSetAccessibility();
                }}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleText(', ', false);
                    setTimeout(() => {
                      handleTouchSetAccessibility();
                    }, 300);
                  }
                }}
              >
                
              </button>
            </div>
          </div>
        </div>
      </>
    );
  }
};

export default AccessibilityModal;
