//  
// basename: BASE_PATH     (/  .env )
//   process.env    
const getBasename = () => {
  //    (  define )
  //  : process   
  //  : process.env.BASE_PATH   
  if (typeof process !== 'undefined' && process.env?.BASE_PATH) {
    return process.env.BASE_PATH;
  }
  return "";
};

//      (BASE_PATH )
const getAssetPath = (path) => {
  const basePath = getBasename();
  //     ,   basePath 
  if (path.startsWith('http://') || path.startsWith('https://')) {
    return path;
  }
  // ./public/...  /public/... 
  const normalizedPath = path.replace(/^\.\//, '/');
  return `${basePath}${normalizedPath}`;
};

const router = createBrowserRouter([
  {
    path: "/",
    element: <LayoutWithFooterOnly />,
    children: [{ index: true, element: <FirstPage /> }],
  },
  {
    path: "/second",
    element: <LayoutWithHeaderAndFooter />,
    children: [{ index: true, element: <SecondPage /> }],
  },
  {
    path: "/third",
    element: <LayoutWithHeaderAndFooter />,
    children: [{ index: true, element: <ThirdPage /> }],
  },
  {
    path: "/forth",
    element: <LayoutWithHeaderAndFooter />,
    children: [{ index: true, element: <ForthPage /> }],
  },
], {
  basename: getBasename()
});

const App = () => {
  // tts api indexedDB 
  const { initDB } = useTextHandler();
  useEffect(() => {
    const initializeDatabase = async () => {
      await initDB();
    };
    initializeDatabase();
  }, [initDB]);


  //  button click   (  pointer-events:none )
  useEffect(() => {
    const handleClick = (event) => {
      const target = event.target;
      updateTimer();
      if (
        target.tagName === "BUTTON" ||
        target.getAttribute("role") === "button"
      ) {
        //      
        if (event.detail !== 0) {
          document.querySelector("#beapSound").volume = 0.5;
          document.querySelector("#beapSound").play();
        }
      }
    };

    document.addEventListener("click", handleClick);
    document.addEventListener('touchstart', function(e){
      e.target.classList.add('pressed');
    });
    document.addEventListener('touchend', function(e){
      e.target.classList.remove('pressed');
    });
    document.addEventListener("touchend", handleClick);
    document.addEventListener('contextmenu', function (e) {
      e.preventDefault(); 
    });
   


  }, []);



  return (
    <AppProvider>
      <audio
        id="audioPlayer"
        src=""
        controls
        style={{
          width: "1px",
          height: "1px",
          position: "absolute",
          margin: "-1px",
          background: "none",
          border: "none",
          zIndex: -1,
        }}
      ></audio>
      <audio
        id="beapSound"
        src={getAssetPath("./public/sound/beap_sound2.mp3")}
        controls
        style={{
          width: "1px",
          height: "1px",
          position: "absolute",
          margin: "-1px",
          background: "none",
          border: "none",
          zIndex: -1,
        }}
      ></audio>
      <RouterProvider router={router} />
    </AppProvider>
  );
};

export default App;
