import React, { useContext, useEffect, useState } from "react";
import { AppContext } from "../App";
import { useNavigate } from "react-router-dom";
// import { startReturnTimer, updateTimer } from "../assets/timer";
import { useTextHandler } from '../assets/tts';

const ReturnModal = ({ }) => {
  const navigate = useNavigate();
  const {
    sections,
    setisLowScreen,
    isHighContrast,
    setisHighContrast,
    totalMenuItems,
    setQuantities,
    isReturnModal,
    setisReturnModal,
    volume,
    setVolume,
    setisBigSize,
    commonScript,
    readCurrentPage
  } = useContext(AppContext);

  const { handleText } = useTextHandler(volume);
  useEffect(() => {
    if (document.activeElement){
      const pageTTS = document.activeElement.dataset.text;
      // document.activeElement.blur(); //   
      setTimeout(() => {
        handleText(pageTTS);
      }, 500); // ""     
    }
  }, [isReturnModal]);

  if (isReturnModal) {
    return (
      <>
        <div className="hidden-div" ref={sections.modalPage}>
          <button type="hidden" autoFocus className="hidden-btn" data-text={", , ,  .     , " + commonScript.replay}></button>
        </div>
        <div
          className="return-modal-overlay"
        ></div>
        <div
          className="return-modal-content"
        >
          <img
            className="return-modal-image"
            src={
              isHighContrast? "/images/contrast-Group 13.png" : "/images/ico_notice.png"
            }
          ></img>
          <div
            className="return-modal-message"
          >
            <p>
              <span
                className="return-highlight"
                style={
                  isHighContrast ? { color: "#FFE101" } : { color: "#A4693F" }
                }
              >
                
              </span>
               
            </p>
            <p>
               {" "}
              <span
                className="return-highlight"
                style={
                  isHighContrast ? { color: "#FFE101" } : { color: "#A4693F" }
                }
              >
                
              </span>{" "}
               
            </p>
          </div>
          <div data-text=",   ," ref={sections.confirmSections} className="return-modal-buttons">
            <button data-text=", "
              className="return-btn-cancel"
              onTouchEnd={(e) => {
                e.preventDefault();
                setisReturnModal(false); readCurrentPage();
              }}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  handleText(', ', false);
                  setTimeout(() => {
                    setisReturnModal(false); readCurrentPage();
                  }, 300);
                }
              }}
            >
              
            </button>
            <button data-text=", "
              className="return-btn-confirm"
              onTouchEnd={(e) => {
                e.preventDefault();
                navigate("/");
                setisReturnModal(false);
              }}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  handleText(', ', false);
                  setTimeout(() => {
                    navigate("/");
                    setisReturnModal(false);
                  }, 300);
                }
              }}
            >
              
            </button>
          </div>
        </div>
      </>
    );
  }
};

export default ReturnModal;
